.journey-section {
  padding: 5rem 0;
  background-color: var(--background);
  transition: background-color 0.3s ease;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.section-subtitle {
  font-size: 1.2rem;
  color: var(--muted-text);
  max-width: 600px;
  margin: 0 auto;
}

.journey-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

@media (min-width: 768px) {
  .journey-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .journey-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.week-card {
  background-color: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: var(--card-shadow);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
}

.week-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--card-shadow-hover);
}

.week-card-header {
  margin-bottom: 1rem;
}

.week-number {
  color: var(--primary-blue);
  font-weight: 600;
  font-size: 0.9rem;
  display: block;
  margin-bottom: 0.25rem;
}

.week-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
}

.week-description {
  color: var(--muted-text);
  line-height: 1.6;
  flex-grow: 1; /* Pushes links to the bottom */
  margin-bottom: 1.5rem; /* Ensure space before topics */
}

.week-topics {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.topic-badge {
  background-color: var(--primary-blue-soft);
  color: var(--primary-blue);
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.8rem;
  font-weight: 500;
}

[data-theme='dark'] .topic-badge {
  color: #e0e7ff;
}

.week-links {
  border-top: 1px solid var(--border-color);
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-top: auto; /* Pushes this block to the bottom of the card */
}

.week-link {
  color: var(--muted-text);
  text-decoration: none;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: color 0.2s ease;
}

.week-link:hover {
  color: var(--primary-blue);
}

.external-link-icon {
  margin-left: auto;
  opacity: 0.6;
}